@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
* {
    margin: 0;
    padding: 0;
}


body {
    font-family: 'Roboto', sans-serif;
    color: #555;
}

.container {
    max-width: 500px;
    margin: 0 auto;
    padding: 0 15px;
    position: relative;
}

ul {
    list-style: none;
}

.user-modal {
    position: absolute;
    top: 0;
    left: 50px;
    z-index: 5;
    height: 105px;
    width: 150px;
    padding: 10px;
    box-shadow: 0 0 15px #555;
    background-color: #fff;
    color: #333;
}

.user-modal li {
    margin-bottom: 20px;
}

.input-field {
    padding: 10px 10px 10px 30px;
    width: 100%;
    color: inherit;
    border: 1px solid lightgray;
}

.button-minus {
    background-color: orangered;
}

.menu-no-save {
    background-color: #007ACC;
    color: #fff;
}

.button-menu {
    border: none;
    padding: 12px 20px;
}
.actions {
    margin-top: 20px;
    display: flex;
}

/* .field-label__success {
    color: #009157;
}

.field-label__warring {
    color: red;
} */

.actions button {
    border: none;
    padding: 10px 5px;
    border-radius: 3px;
    outline: none;
    margin-right: 15px;
    color: #fff;
    font-size: 15px;
    flex-grow: 1;
    max-width: 160px;
}

.actions button:last-child {
    /* margin-left: auto; */
    margin-right: 0;
    background-color: #007ACC;
}

.button-plus {
    background-color: #009157;
}

html {
    box-sizing: border-box;
  }
  *, *:before, *:after {
    box-sizing: inherit;
  }

.App-header {
    background-color: #007ACC;
    color: #fff;
    padding: 15px 0;
    margin-bottom: 10px;
    font-size: 12px;
}

.App-header p {
    line-height: 1.5;
    /* font-weight: 700; */
    color: #fefefe;
}



.total {
    display: flex;
    justify-content: space-between;
    outline: none;
    border: none;
}


.field {
    position: relative;

}

.success {
    font-size: 25px !important;
    color: red;
    top: 3px !important;
}

.field-no-save {
    background-color: yellow;
}


.warring {
    color: #009157;
}

.field-content {
    margin-bottom: 10px;
}

.field-label {
    position: relative;
    flex-grow: 1;
    display: flex;
}

.date {
    font-size: 12px;
    color: gray;
    margin-bottom: 5px;
}

.field-label span {
    position: absolute;
    top: 10px;
    left: 10px;
    font-size: 18px;

}

.field-content p {
    font-size: 13px;
    margin-left: 10px;

}

.field-modal {
    position: absolute;
    z-index: 1;
    top: 0;
    right: 15px;
    display: flex;
    flex-direction: column;
    width: 150px;
    padding: 5px;
    background-color: #fff;
    box-shadow: 0 0 5px #cccccc;
}

.field-modal button {
    margin-bottom: 10px;
}
